class Socket {
	constructor(config) {
		// 
		this.Config = Object.assign(
			{
				autoHeartBeat: true,
				heartBeatInterval: 1,
				reconnect: true,
				reonnectInterval: 1
			},
			config || {}
		);
		// 
		this.event = {};
		// 
		this.server = {};
		// 
		this.status = false;
		// 
		this.nologin = false;
		// 
		this.error = false;
		// 
		this.t = null;
		// 
		this.ct = null;
		// callback
		this.callback = null;
		// Global
		this.Global = {};
		// StartTimes
		this.StartTimes = 0;
		this.OnError = null;
		this.OnClose = null;
		this.OnOpen = null;
		this.OnSend = null;
		this.OnMessage = null;
	}

	Close(e) {
		this.server.close();
		this.nologin = true;
		this.server = null;
		this.callback = null;
		this.event = null;
		this.status = false;
		this.Global = null;
		this.Config = null;
		this.OnError = null;
		this.OnClose = null;
		this.OnOpen = null;
		clearInterval(this.t);
		clearTimeout(this.ct);
		this.OnClose && this.OnClose(e);
	}

	Start(callback) {
		this.connect();
		// 
		this.callback = callback || function() {};
	}

	connect() {
		let index = this.Config.host.indexOf("://");
		let prefix = this.Config.host.slice(0, index);
		let host = this.Config.host.slice(index + 3);
		let port = this.Config.port;
		let path = this.Config.path;

		let proto = "ws://";

		switch (prefix) {
			case "https":
				proto = "wss://";
				port = port || 443;
				break;

			case "http":
				proto = "ws://";
				port = port || 80;
				break;

			case "wss":
				proto = "wss://";
				port = port || 443;
				break;

			case "ws":
				proto = "ws://";
				port = port || 80;
				break;

			default:
				proto = "ws://";
				port = port || 80;
				break;
		}

		// console.log("", `${proto}${host}:${port}${path || "/"}`);

		// 
		clearInterval(this.t);
		clearTimeout(this.ct);
		// WS
		// NODE
		if (typeof module !== "undefined" && module.exports) {
			var W3CWebSocket = require("websocket").w3cwebsocket;
			this.server = new W3CWebSocket(`${proto}${host}:${port}${path || "/"}`);
		} else {
			this.server = new WebSocket(`${proto}${host}:${port}${path || "/"}`);
		}

		// 
		this.server.onopen = this.onopen.bind(this);
		// 
		this.server.onmessage = this.onmessage.bind(this);
		// 
		this.server.onclose = this.onclose.bind(this);
		// 
		this.server.onerror = this.onerror.bind(this);
		// 
		if (this.Config.autoHeartBeat) this.HeartBeat();
	}

	onopen() {
		this.status = true;
		this.error = false;
		this.StartTimes++;
		this.OnOpen && this.OnOpen();
		//   
		this.callback.call(this, this);
		// console.log("");
	}

	onmessage(e) {
		this.OnMessage && this.OnMessage(e);
		// 
		try {
			let message = JSON.parse(e.data);

			if (message.event && message.event in this.event) {
				this.event[message.event].call(this, this, message.data, message.event);
			}

			if (this.event["*"]) this.event["*"].call(this, this, message.data, message.event);
		} catch (error) {
			console.log("socket:", error);
		}
	}

	onclose(e) {
		this.status = false;
		this.OnClose && this.OnClose(e);
		// :   
		clearInterval(this.t);
		if (this.nologin) return false;
		if (this.Config.reconnect) {
			this.ct = setTimeout(() => {
				if (this.error) return false;
				console.log("close");
				this.connect();
			}, 1000 * this.Config.reonnectInterval || 1000);
		}
	}

	onerror(e) {
		this.error = true;
		this.status = false;
		this.OnError && this.OnError(e);
		// :   
		clearInterval(this.t);
		if (this.nologin) return false;

		if (this.Config.reconnect) {
			this.ct = setTimeout(() => {
				console.log("error");
				this.connect();
			}, 1000 * this.Config.reonnectInterval || 1000);
		}
	}

	HeartBeat() {
		//   WEBSOCKETPING PONG
		this.t = setInterval(() => {
			try {
				if (this.nologin || this.error || !this.status) return false;
				this.server.send("");
			} catch (error) {
				console.log(":", error);
			}
		}, 1000 * this.Config.heartBeatInterval || 5000);
	}

	Send(message) {
		this.OnSend && this.OnSend(message);
		this.server.send(message);
	}

	Emit(event, message) {
		// 
		if (this.nologin || this.error || !this.status) return false;

		try {
			if (!message) {
				this.server.send(JSON.stringify({ event: event }));
				return true;
			}

			let data = null;
			if (typeof message !== "object") {
				data = message;
			} else {
				data = { ...message, ...this.Global };
			}

			this.OnSend && this.OnSend({ event: event, data: data });

			this.server.send(JSON.stringify({ event: event, data: data }));

			return true;
		} catch (error) {
			console.log(":", error);
			return false;
		}
	}

	AddListener(event, callback) {
		// 
		if (!this.event[event]) this.event[event] = callback;
	}

	RemoveListener(event) {
		// 
		this.event && delete this.event[event];
	}
}

module.exports = Socket;
